<template>
  <section :class="$style.container">

    <h1 :class="$style.cta">
      <span>Have a project in mind?</span>
      <call-to-action>Tell Us About It</call-to-action>
    </h1>


    <div :class="$style.info">

      <a>
        <fa-icon :icon="fasEnvelope"/>
        <span>{{ company.email }}</span>
      </a>

      <a>
        <fa-icon :icon="fasPhone"/>
        <span>{{ company.phone }}</span>
      </a>

      <social-bar :class="$style.social" />

    </div>
  </section>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex';
  const { mapState } = createNamespacedHelpers('company');

  import CallToAction from '@/components/CallToAction';
  import SocialBar from '@/components/SocialBar';
  import FaIcon from '@fortawesome/vue-fontawesome';
  import {
    faEnvelope as fasEnvelope,
    faPhone as fasPhone,
  } from '@fortawesome/fontawesome-free-solid';

  export default {
    components: {
      CallToAction,
      FaIcon,
      SocialBar,
    },

    computed: {
      ...mapState([
        'company',
      ]),

      fasEnvelope(){
        return fasEnvelope;
      },

      fasPhone(){
        return fasPhone;
      },
    },
  };
</script>

<style module>
  .container {
    display: grid;
    grid-gap: 3em;
    justify-content: center;
    align-content: center;

    background-color: var(--neutral);
    color: white;

    height: 20em;
    text-align: center;
  }

  .info {
    display: grid;
    grid-gap: var(--grid-spacing);
  }

  .cta {
    display: grid;
    grid-gap: var(--grid-spacing);
  }

  .social {
  }
</style>



