<template>
  <router-link
    tag="article"
    :class="$style.container"
    :to="project.id"
    append
  >

    <router-link
      tag="figure" 
      :class="$style.hero"
      :to="project.id"
      append
    >
      <cf-image
        :asset="project.hero.fields"
      />
    </router-link>

    <header :class="$style.title">
      <a>{{ project.title }}</a>
    </header>

  </router-link>
</template>

<script>
  import CfImage from '@/components/CFImage';

  export default {
    components: {
      CfImage,
    },

    props: {
      project: Object,
    },
  }
</script>

<style module>
  .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    align-items: end;

    position: relative;

    cursor: pointer;
  }
  
  .hero {
    grid-column: 1 / span 3;
    grid-row: 1 / span 3;

    height: 100%;
  }

  .hero > * {
    min-width: 100%;
    min-height: 100%;

    max-width: 100%;
    max-height: 100%;

    object-fit: cover;
    object-position: center;
  }

  .title {
    grid-column: 1 / span 3;
    grid-row: 3;

    display: grid;
    justify-content: end;

    padding: 0.33em;
  }

  .title > * {
    background-color: white;
    padding: 0.33em 0.66em;
  }
</style>



